import './Etusivu.css';
import kuva from './clean.jpg';
import kuva_1 from './clean_1.jpg';
import kuva_2 from './clean_2.jpg';
import kuva_3 from './clean_3.jpg';
import kuva_4 from './clean_4.jpg';


const Etusivu = () => {
    return (
        <div
        >
            <h1>Tervetuloa PuhtaanapitoPlus siivouspalvelun sivuille!</h1>
            <p>Tarjoamme ammattitaitoista ja luotettavaa siivouspalvelua niin kotitalouksille kuin yrityksillekin. Meidn kokenut henkilkuntamme huolehtii kodin, toimiston tai vaikkapa saunan puhtaudesta jtten asiakkaan ajan muuhun kyttn.</p>
            <div className="card">
                <div className="image-container">
                    <img src={kuva} className="clean" alt="clean" />
                </div>
                <div className="content-container">
                    <h2>Kotisiivous</h2>
                    <p>Kotisiivouksessa kymme lpi kotisi tarpeet ja tarjoamme juuri sinun tarpeisiisi sopivan siivouspaketin. Voit valita mink tahansa palveluistamme tai valita useamman palvelun yhdistelmn, jotta kotisi on aina juuri sellaisessa kunnossa kuin haluat.</p>
                </div>
            </div>
            <div className="card">
                <div className="image-container">
                    <img src={kuva_1} className="clean_1" alt="clean_1" />
                </div>
                <div className="content-container">
                    <h2>Toimistosiivous</h2>
                    <p>Toimistosiivouksessa huomioimme juuri teidn toimistonne tarpeet. Teemme tyt nopeasti ja tehokkaasti, jotta tyntekijt voivat keskitty omaan tyhns. Meill on vuosien kokemus toimistosiivouksesta ja olemme ylpeit siit, ett olemme saaneet palvella monia asiakkaita menestyksekksti.</p>
                </div>
            </div>
            <div className="card">
                <div className="image-container">
                    <img src={kuva_2} className="clean_2" alt="clean_2" />
                </div>
                <div className="content-container">
                    <h2>Saunan siivous</h2>
                    <p>Saunan siivous on erikoisosaamistamme ja voimme taata ett asiakkaan sauna on aina puhtaana ja hygieenisen. Saunan siivous sislt mm. lauteiden pesun ja lattian pesun, sek seinien pesun. Tarjoamme saunan siivouksen yksittispalveluna tai osana kotisiivouspakettia.</p>
                    <img src={kuva_3} alt="saunansiivous" />
                </div>
                <div className="content-container">
                    <h2>Yrityssiivous</h2>
                    <p>Tarjoamme yrityksille kattavan siivouspalvelun, johon kuuluu mm. toimistojen, myymliden ja muiden liikehuoneistojen siivous. Meidn henkilkuntamme on koulutettu ksittelemn erilaisia siivousmenetelmi, joten voit luottaa siihen, ett toimistosi on aina puhtaana ja siistin.</p>
                    <img src={kuva_4} alt="Yrityssiivouskuva" />
                </div>
            </div>
        </div>
    );
};

export default Etusivu;
